import React, { PropTypes, Component } from 'react';
import styles from './ManagePage.css';
import withStyles from '../../decorators/withStyles';

import {Grid,Row,Col,Panel,Table,Button,Modal,Input} from 'react-bootstrap';
import $ from 'jquery';

class EditModal extends Component {
	state = {
		value:'hello'
	}

	_onChange = (e) => {
		this.setState({
			value:e.target.value
		})
	}
	submitEdit(e){
	    var $textarea = $(e.target).parent().prev().find('textarea');
	    var text = $textarea.val();
	    var key = e.target.key;
	    console.log(key);
	    debugger;
	}

	render(){
		return (
			<Modal
	            show={this.props.showName}
	            onHide={this.props.hideAction}
	            container={this}
	            aria-labelledby="contained-modal-title">
	            <Modal.Header closeButton>
	              <Modal.Title id="contained-modal-title">{this.props.modalName}</Modal.Title>
	            </Modal.Header>
	            <Modal.Body>
	              <Input type="textarea" label="Text Area" placeholder="textarea" value={this.state.value} onChange={this._onChange} className="content-team"/>
	            </Modal.Body>
	            <Modal.Footer>
	              <Button onClick={this.props.hideAction}>关闭</Button>
	              <Button onClick={this.submitEdit.bind(this)} key='team'>提交</Button>
	            </Modal.Footer>
	         </Modal>
		)
	}
};
export default EditModal;


