import React, { Component } from 'react';
import styles from './Feedback.css';
import withStyles from '../../decorators/withStyles';
import {Nav,NavItem,NavDropdown,MenuItem,Button} from 'react-bootstrap';
import $ from 'jquery';
//import bootstrapCss from './bootstrap.min.css';


@withStyles(styles)

class Feedback extends Component {
  componentDidMount() {

    $('body').css({"color":"green"});
  }

  handleSelect(event, selectedKey) {
    event.preventDefault();
    alert('selected ' + selectedKey);
    // onSelect={this.handleSelect}
  }

  render() {
    return (
      <div className="Feedback">
      <Button bsStyle="primary">Primary</Button>
        <div className="Feedback-container">
          <a className="Feedback-link" href="https://gitter.im/kriasoft/react-starter-kit">Ask a question</a>
          <span className="Feedback-spacer">|</span>
          <a className="Feedback-link" href="https://github.com/kriasoft/react-starter-kit/issues/new">Report an issue</a>
        </div>
        <Nav bsStyle="tabs" activeKey={1} >
          <NavItem eventKey={1} href="/home">NavItem 1 content</NavItem>
          <NavItem eventKey={2} title="Item">NavItem 2 content</NavItem>
          <NavItem eventKey={3} disabled>NavItem 3 content</NavItem>
          <NavDropdown eventKey={4} title="Dropdown" id="nav-dropdown" >
            <MenuItem eventKey="4.1" onSelect={this.handleSelect}>Action</MenuItem>
            <MenuItem eventKey="4.2" onSelect={this.handleSelect}>Another action</MenuItem>
            <MenuItem eventKey="4.3" onSelect={this.handleSelect}>Something else here</MenuItem>
            <MenuItem divider />
            <MenuItem eventKey="4.4" onSelect={this.handleSelect}>Separated link</MenuItem>
          </NavDropdown>
        </Nav>
      </div>
    );
  }

}

export default Feedback;



