import React, { PropTypes, Component } from 'react';
import styles from './ManagePage.css';
import withStyles from '../../decorators/withStyles';

import TableInfo from './TableInfo';

import {Grid,Row,Col,Panel,Table,Button,Modal,Input} from 'react-bootstrap';
import $ from 'jquery';

@withStyles(styles)


class ManagePage extends Component {
  /**
   * ES6语法没有getInitialState，改用state
   * @type {Object}
   */
  state = {
    /*showModal:{
      teamShow: false ,
      structShow: false,
      teachShow: false
    },*/
    content:{
      team:'kevin',
      struct:'',
      teach:''
    }
  }

  /**
   * 定义class方法
   * @param  {[type]} e [description]
   * @return {[type]}   [description]
   */
  /*teamShow = (e) =>{
    this.setState({ teamShow: true })
  }

  structShow = (e) =>{
    this.setState({ structShow: true })
  }

  teachShow = (e) =>{
    this.setState({teachShow: true})
  }

  teamClose = (e) =>{
    this.setState({ teamShow: false })
  }
  structClose = (e) =>{
    this.setState({ structShow: false })
  }
  teachClose = (e) =>{
    this.setState({teachShow: false})
  }*/

  static contextTypes = {
    onSetTitle: PropTypes.func.isRequired,
  }

 

  

  componentDidMount(){
    function ajaxGet() {    
      var url = "http://beta.json-generator.com/api/json/get/VJSqJ8LNl";
      $.ajax({
          cache: false,
          type:"GET",
          dataType: "JSONP",
          url: url,
          success: function(data, textStatus){
            if (data[0].code==0) {
              
              debugger;
            };
              console.log(data[0].data);
              debugger;
          },
          error:function(e){
                console.log(e.toString());
                debugger;
            }.bind(this),
          complete: function(XMLHttpRequest, textStatus){

          }
      });
    };

    ajaxGet();
  }

  _onChange = (e) => {
    
  }

  render() {
    const title = '管理';
    this.context.onSetTitle(title);



    return (
      <div className="ManagePage">
        <div className="ManagePage-container">

          

          <TableInfo content={this.state.content} showModal={this.state.showModal}/>
        </div>
      </div>
    );
  }
}
export default ManagePage;



