import React, { PropTypes, Component } from 'react';
import styles from './ManagePage.css';
import withStyles from '../../decorators/withStyles';
import EditModal from './EditModal.js'

import {Grid,Row,Col,Panel,Table,Button,Modal,Input} from 'react-bootstrap';
import $ from 'jquery';

class TableInfo extends Component {

	state = {
	      teamShow: false ,
	      structShow: false,
	      teachShow: false
	}


  /**
   * 定义class方法
   * @param  {[type]} e [description]
   * @return {[type]}   [description]
   */
  teamShow = (e) =>{
    this.setState({ teamShow: true })
  }

  structShow = (e) =>{
    this.setState({ structShow: true })
  }

  teachShow = (e) =>{
    this.setState({teachShow: true})
  }

  teamClose = (e) =>{
    this.setState({ teamShow: false })
  }
  structClose = (e) =>{
    this.setState({ structShow: false })
  }
  teachClose = (e) =>{
    this.setState({teachShow: false})
  }

	render(){
		const team = "团队简介";
		const struct = "组织结构";
		const teach = "名师风采";

		return (
			
            <Grid>
	            <Row className="show-grid">
	                <Col xs={12} md={8}>
	                <EditModal showName={this.state.teamShow} hideAction={this.teamClose} showAction={this.teamShow} modalName={team}/>
				    <EditModal showName={this.state.structShow} hideAction={this.structClose} showAction={this.structShow} modalName={struct}/>
				    <EditModal showName={this.state.teachShow} hideAction={this.teachClose} showAction={this.teachShow} modalName={teach}/>
	                <Table striped bordered condensed hover>
	                  <thead>
	                    <tr>
	                      <th>#</th>
	                      <th>名称</th>
	                      <th>内容</th>
	                      <th>操作</th>
	                    </tr>
	                  </thead>
	                  <tbody>
	                    <tr>
	                      <td>1</td>
	                      <td>{team}</td>
	                      <td>{this.props.content.team}</td>
	                      <td><Button bsStyle="primary" bsSize="large" onClick={this.teamShow} >编辑</Button></td>
	                    </tr>
	                    <tr>
	                      <td>2</td>
	                      <td>{struct}</td>
	                      <td>Thornton</td>
	                      <td><Button bsStyle="primary" bsSize="large" onClick={this.structShow} >编辑</Button></td>
	                    </tr>
	                    <tr>
	                      <td>3</td>
	                      <td >{teach}</td>
	                      <td>Otto</td>
	                      <td><Button bsStyle="primary" bsSize="large" onClick={this.teachShow} >编辑</Button></td>
	                    </tr>
	                  </tbody>
	                </Table>
	              </Col>
	            </Row>
          	</Grid>


		);

	}
}
export default TableInfo;


