import React, { PropTypes, Component } from 'react';
import styles from './LoginPage.css';
import withStyles from '../../decorators/withStyles';
import {Input,ButtonInput,Button} from 'react-bootstrap';
import $ from 'jquery';

@withStyles(styles)
class LoginPage extends Component {
  
  componentDidMount(){
    function Login() {  
      var data = {
        username: $('.username').val(),
        password: $('.password').val()
      }; 
      debugger; 
      /**发送登录Ajax
       * [url api from express server]
       * @type {String}
       */
      var url = "login.php"; 
      $.ajax({
          cache: false,
          type:"POST",
          data:data,
          dataType: "JSON",
          url: url,
          success: function(data, textStatus){
              console.log(data);
              if(data.code == 0){
                
                debugger;
                window.location.pathname = '/manage';
              }
              
          },
          error:function(e){
                console.log(e.toString());
                debugger;
            }.bind(this),
          complete: function(XMLHttpRequest, textStatus){

          }
      });
    };
    /**Login ajax
     * @param  {[type]}
     * @return {[type]}
     */
    $('.login').on('click',function(e){
      e.preventDefault();
      Login();
    })
  }

  static contextTypes = {
    onSetTitle: PropTypes.func.isRequired,
  };

  render() {
    const title = 'Log In';
    this.context.onSetTitle(title);
    return (
      <div className="LoginPage">
        <div className="LoginPage-container">
          <form>
            <Input type="text" label="用户名" placeholder="Enter text" className="username"/>
            <Input type="password" label="密码" placeholder="Enter password" className="password"/>
            <Button bsStyle="primary" type="submit" value="登陆" className="login">登陆</Button>
            <a type="Button" href="/register" value="注册" className="register">注册</a>
          </form>
        </div>
      </div>
    );
  }

}

export default LoginPage;



