import React, { PropTypes, Component } from 'react';
import classNames from 'classnames';
import styles from './Navigation.css';
import withStyles from '../../decorators/withStyles';
import Link from '../Link';

@withStyles(styles)
class Navigation extends Component {

  static propTypes = {
    className: PropTypes.string,
  };

  render() {
    return (
      <div className={classNames(this.props.className, 'Navigation')} role="navigation">
        <a className="Navigation-link" href="/about" onClick={Link.handleClick}>简介</a>
        <a className="Navigation-link" href="/project" onClick={Link.handleClick}>项目</a>
        <span className="Navigation-spacer"> | </span>
        <a className="Navigation-link" href="/awards" onClick={Link.handleClick}>获奖</a>
        <span className="Navigation-spacer">or</span>
        <a className="Navigation-link Navigation-link--highlight" href="/register" onClick={Link.handleClick}>招新</a>
      </div>
    );
  }

}

export default Navigation;



